import {
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Label,
  CustomInput
} from 'reactstrap' 
import React, { useState, useEffect, Fragment } from 'react'
import BreadCrumbs from '@components/breadcrumbs'
import { footballteamsviwe, footballteamsedit } from '../../store/actions'
import { useDispatch, useSelector } from 'react-redux'
import '@styles/react/libs/flatpickr/flatpickr.scss'


const VerticalForm = () => {
  
  const dispatch = useDispatch()
  const store = useSelector((state) => state.teams)
  const {football_team_view} = store
  console.log(store)
  
  const [editteamsform, setEditteamsform] = useState()
  const [check, setCheck] = useState(false)
  useEffect(() => {
    dispatch(footballteamsviwe)
  }, [football_team_view])
  useEffect(() => {
    setEditteamsform({ team_name:football_team_view?.team_name, team_sn:football_team_view?.team_sn, team_api:football_team_view?.team_api, team_api_id:football_team_view?.team_api_id, team_logo:football_team_view?.team_logo, path:football_team_view?.path})
  }, [football_team_view])
  console.log(football_team_view?.id)
  console.log(editteamsform)
  const onSubmit = (e) => {
    e.preventDefault()
    console.log(editteamsform)
    if (check === true) {
      const params = {
        team_name: editteamsform?.team_name,
        team_sn: editteamsform?.team_sn, 
        team_api: editteamsform?.team_api,
        team_api_id: editteamsform?.team_api_id,
        team_logo: editteamsform?.team_logo,
        path : editteamsform?.path
      }
      dispatch(footballteamsedit(football_team_view.id, params))
    } else {
      const params1 = {
        team_name: editteamsform?.team_name,
        team_sn: editteamsform?.team_sn, 
        team_api: editteamsform?.team_api,
        team_api_id: editteamsform?.team_api_id,
        team_logo: editteamsform?.team_logo,
        path:''
        
      }
      dispatch(footballteamsedit(football_team_view.id, params1))
    }
   
  }

 const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader()
      fileReader.readAsDataURL(file)
      
      fileReader.onload = () => {
        resolve(fileReader.result)
      }
      
      fileReader.onerror = (error) => {
        reject(error)
      }
    })
  }
  
  const uploadImage = async (e) => {
    const file = e.target.files[0]
    const base64 = await convertBase64(file)
    setEditteamsform({ ...editteamsform, path: base64 })
    setCheck(true)
  }

  return (
    <Fragment>
          <BreadCrumbs breadCrumbTitle='Football Teams' breadCrumbParent='footballteams' breadCrumbActive='Edit' />
    <Card>
    <CardHeader>
      <CardTitle tag="h4">Edit Cricket Teams</CardTitle>
    </CardHeader>
    <CardBody>
      <Form>
        <Row>
        {/* <Col sm="12">
            <FormGroup>
              <Label for="EmailVertical">Id</Label>
              <Input
                type="number"
                name="Email"
                id="EmailVertical"
                value={editteamsform?.id}
                onChange={(e) => setEditteamsform({ ...editteamsform, id: e.target.value })}
                placeholder="Enter No of ID "
              />
            </FormGroup>
          </Col> */}
          <Col sm="12">
            <FormGroup>
              <Label>Team Name</Label>
              <Input
                type="name"
                name="name"
                id="nameVertical"
                value={editteamsform?.team_name}
                onChange={(e) => setEditteamsform({ ...editteamsform, team_name: e.target.value })}
                placeholder="Enter Product Name"
              />
            </FormGroup>
          </Col>
          <Col sm="12">
            <FormGroup>
              <Label for="nameVertical">Series sh</Label>
              <Input
                type="text"
                name="name"
                id="nameVertical"
                value={editteamsform?.team_sn}
                onChange={(e) => setEditteamsform({ ...editteamsform, team_sn: e.target.value })}
                placeholder="Enter Product Name"
              />
            </FormGroup>
          </Col>
          <Col sm="12">
            <FormGroup>
              <Label for="nameVertical">Team Api</Label>
              <Input
                type="text"
                name="name"
                id="nameVertical"
                value={editteamsform?.team_api}
                onChange={(e) => setEditteamsform({ ...editteamsform, team_api: e.target.value })}
                placeholder="Enter Product Name"
              />
            </FormGroup>
          </Col>
          <Col sm="12">
            <FormGroup>
              <Label for="nameVertical">Team Api id</Label>
              <Input
                type="text"
                name="name"
                id="nameVertical"
                value={editteamsform?.team_api_id}
                onChange={(e) => setEditteamsform({ ...editteamsform, team_api_id: e.target.value })}
                placeholder="Enter Product Name"
              />
            </FormGroup>
          </Col>
          <Col sm="12">
              <FormGroup>
                <Label for="exampleCustomFileBrowser">Custom File Input</Label>
                <CustomInput
                  type="file"
                  id="exampleCustomFileBrowser"
                  name="customFile"
                  onChange={(e) => uploadImage(e)}
                />
                <div className="logo">
                <img src={editteamsform?.path} width="100" height="50" />
               </div>
              </FormGroup>
            </Col>
          
        <Col sm="12">
            <FormGroup className="d-flex mb-0">
              <Button.Ripple
                className="mr-1"
                color="primary"
                type="submit"
                onClick={(e) => onSubmit(e)}
              >
                Submit
              </Button.Ripple>
            </FormGroup>
          </Col>
        </Row>
      </Form>
    </CardBody>
  </Card>
  </Fragment>
  )
}
export default VerticalForm
